<template>
    <tile :actions="actions" :columns="columns" :icon="icon" :loading="loading" :nopadding="true" :title="title"
          class="doc-grid">
        <div class="section">
            <div class="section-table nomargin">
                <grid :columns="columns" :data="data" :descending="true" :row-marker="markRow"
                      key-prop="Id" load-sorted-by="DateCreated" no-data-text=""></grid>
            </div>
        </div>
    </tile>
</template>

<script>
export default {
    props: {
        data: Array,
        title: String,
        icon: String,
        actions: Array,
        columns: Array,
        loading: Boolean
    },
    methods: {
        markRow(row) {
            return row.IsNew ? 'new' : '';
        }
    }
}
</script>


<style lang="scss">
.doc-grid {
    tr.new td {
        font-weight: bold !important;
    }
}
</style>
