<template>
    <div class="my-documents">
        <own-docs v-show="view=='grid'" :actions="actions" :data="ownDocs" :loading="loading"></own-docs>
        <shared-docs v-show="view=='grid' && sharedDocs.length > 0" :data="sharedDocs"></shared-docs>
        <company-docs v-show="view=='grid' && compDocs.length > 0" :data="compDocs"></company-docs>
        <upload v-show="view == 'upload'" :docs="ownDocs" :is-new-upload="uploadNew" :tasks="uploads"
                @close="onUploadClose"
                @complete="onUploadComplete" @doc-created="onDocumentCreated" @doc-updated="onDocumentUpdated"></upload>
        <modal :visible="showAcceptanceWarning" class="acceptance-modal" @close="closeAcceptanceWarning">
            <div class="section">
                <div class="default-form">
                    <div class="form-row">
                        <p>This document requires acceptance. Please review it and either Accept or Decline.</p>
                    </div>
                </div>
                <div class="form-row row-buttons">
                    <button class="blue" type="submit" @click="onDocumentDownload">
                        <span class="icon-download"></span>
                        Download
                    </button>
                    <button class="grey" type="submit" @click="closeAcceptanceWarning">
                        <span class="icon-blocked"></span>
                        Cancel
                    </button>
                </div>
            </div>
        </modal>
        <modal :visible="showAcceptance" class="acceptance-modal" @close="closeAcceptance">
            <div class="section">
                <div class="default-form">
                    <div class="form-row">
                        <p>{{ acceptanceText }}</p>
                    </div>
                </div>
                <div class="form-row row-buttons">
                    <button class="green" type="submit" @click="onDocumentAccepted">
                        <span class="icon-tick"></span>
                        I Accept
                    </button>
                    <div class="button red" @click="onDocumentDeclined">
                        <span class="icon-blocked"></span>
                        I Decline
                    </div>
                </div>
            </div>
        </modal>
    </div>
</template>

<script>
import {mapActions} from 'vuex';
import OwnDocs from './own.vue';
import SharedDocs from './shared.vue';
import CompanyDocs from './company.vue';
import Upload from './upload.vue';
import eventhub from './eventhub';

export default {
    components: {
        OwnDocs,
        SharedDocs,
        CompanyDocs,
        Upload
    },
    data() {
        return {
            view: 'grid',
            saving: false,
            loading: true,
            uploading: 0,
            failed: 0,
            uploadNew: false,
            ownDocs: [],
            sharedDocs: [],
            compDocs: [],
            uploads: [],
            showAcceptance: false,
            showAcceptanceWarning: false,
            acceptanceText: '',
            acceptDocument: null,
            declineDocument: null,
            downloadDocument: null
        }
    },
    computed: {
        actions() {
            let actions = [{
                label: 'Upload file',
                icon: 'icon-upload',
                color: 'green',
                click: () => {
                    this.view = 'upload';
                    this.uploadNew = true;
                }
            }];

            let progress = {
                color: 'blue',
                click: () => {
                    this.view = 'upload';
                    this.uploadNew = false;
                }
            }

            if (this.uploading) {
                progress.icon = 'icon-cycle';
                progress.label = `${this.uploading} files uploading...`;
                actions.unshift(progress);
            } else if (this.failed) {
                progress.icon = 'icon-warning';
                progress.label = `${this.failed} files failed to upload`;
                progress.color = 'red';
                actions.unshift(progress);
            }

            return actions;
        }
    },
    watch: {
        uploads: {
            deep: true,
            handler(uploads) {
                this.failed = 0;
                this.uploading = 0;

                uploads.forEach(u => {
                    if (u.status == 'queued' || u.status == 'sending') {
                        this.uploading++;
                    } else if (u.status == 'error') {
                        this.failed++;
                    }
                });
            }
        }
    },
    created() {
        this.fetchDocs();
        eventhub.$on('acceptance-warning', this.onDocumentAcceptanceWarning);
        eventhub.$on('acceptance-required', this.onDocumentAcceptanceRequired);
    },
    beforeDestroy() {
        eventhub.$off('acceptance-warning', this.onDocumentAcceptanceWarning);
        eventhub.$off('acceptance-required', this.onDocumentAcceptanceRequired);
    },
    methods: {
        ...mapActions([
            'addToast',
            'showRequestError'
        ]),
        fetchDocs() {
            this.loading = true;

            this.$ajax.post('/api/Documents/GetDocuments')
                .then(resp => {
                    let docs = resp.data;

                    this.ownDocs = [];
                    this.sharedDocs = [];
                    this.compDocs = [];

                    (docs || []).forEach((doc) => {
                        switch (doc.Category) {
                            case 'My':
                                this.ownDocs.push(doc);
                                break;
                            case 'Shared':
                                this.sharedDocs.push(doc);
                                break;
                            case 'Company':
                                this.compDocs.push(doc);
                                break;
                        }
                    });
                })
                .catch(resp => {
                    this.showRequestError({
                        resp,
                        title: 'Could not retrieve documents'
                    });
                })
                .then(() => {
                    this.loading = false;
                });
        },
        save() {
            this.saving = true;
        },
        closeAcceptance() {
            this.showAcceptance = false;
            this.acceptDocument = null;
            this.declineDocument = null;
        },
        closeAcceptanceWarning() {
            this.showAcceptanceWarning = false;
            this.downloadDocument = null;
        },
        onUploadClose() {
            this.view = 'grid';
        },
        onUploadComplete() {
            if (this.uploads.length > 0) this.addToast({
                title: 'Upload failed',
                type: 'error',
                message: 'Some files have failed to upload. Click this box to review errors.',
                onClick: () => {
                    this.uploadNew = false;
                    this.view = 'upload';
                }
            })
            else this.addToast({
                title: 'Files uploaded',
                type: 'success',
                message: 'All files have been successfully uploaded.'
            })
        },
        onDocumentCreated(doc) {
            this.ownDocs.push(doc);
        },
        onDocumentUpdated(doc) {
            for (let i = 0; i < this.ownDocs.length; i++) {
                if (doc.Id == this.ownDocs[i].Id) {
                    this.$set(this.ownDocs, i, doc);
                    break;
                }
            }
        },
        onDocumentAcceptanceWarning({download}) {
            this.showAcceptanceWarning = true;
            this.downloadDocument = download;
        },
        onDocumentAcceptanceRequired({text, accept, decline}) {
            this.acceptanceText = text;
            this.showAcceptance = true;
            this.acceptDocument = accept;
            this.declineDocument = decline;
        },
        onDocumentDownload() {
            this.downloadDocument();
            this.closeAcceptanceWarning();
        },
        onDocumentAccepted() {
            this.acceptDocument();
            this.closeAcceptance();
        },
        onDocumentDeclined() {
            this.declineDocument();
            this.closeAcceptance();
        }
    }
}
</script>

<style lang="scss">
.my-documents {
    .ui-grid {
        .col-ownername,
        .col-type-date {
            width: 185px;
        }

        .col-buttons {
            width: 175px;
        }
    }

    .acceptance-modal {
        .uimodalbody {
            min-height: 50px !important;
        }
    }

    .uimodalbody {
        .section {
            position: relative;
            padding-bottom: 40px;
            box-sizing: border-box;
        }

        .row-buttons {
            position: absolute;
            bottom: -5px;
            text-align: right;
            display: inline-block;
            width: 100%;
            //padding-right: 15px;
            box-sizing: border-box;
        }
    }
}
</style>
