<template>
    <tile
        :class="{ expanded: emp }"
        :loading="loading"
        :nopadding="true"
        class="search"
        icon="icon-search"
        title="Staff Directory Search">
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div class="form-row">
                        <input v-model="query"
                               class="search-editor"
                               placeholder="Search for an employee"
                               type="text"
                               @input="onSearchInput"/>
                    </div>
                    <div class="form-row">
                        <div v-if="!searched && searching" class="search-result-summary">
                            Searching...
                        </div>
                        <div v-else-if="searched" class="search-result-summary">
                            {{ searchSummaryText }}
                        </div>
                    </div>
                    <div v-if="employees.length > 0" class="form-row row-search-result">
                        <div class="col-search-result">
                            <emp-list
                                :data="employees"
                                :info-config="infoConfig"
                                :level="0"
                                :max-level="2"
                                :selection-mode="true"
                                @selection-change="onListSelectionChange">
                            </emp-list>
                        </div>
                        <div v-if="emp !== null" class="col-emp-info">
                            <emp-info
                                :config="infoConfig"
                                :data="emp"
                                :level="1"
                                :max-level="2">
                            </emp-info>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>


<script>
import {debounce, throttle} from "lodash";
import Remote from "../../remote";
// import EmpInfo from "./EmpInfo.vue";
// import EmpList from "./EmpList.vue";


export default {
    props: {
        configHost: String
    },
    // components: {
    //     EmpInfo,
    //     EmpList,
    // },
    data() {
        return {
            query: null,
            queryValid: false,
            expanded: false,
            emp: null,
            searched: false,
            searching: false,
            employees: [],
            loading: true,
            searchRequest: null,
            infoConfig: []
        };
    },
    computed: {
        searchSummaryText() {
            return this.employees.length > 0
                ? `Found ${this.employees.length} employees`
                : "No results found.";
        }
    },
    methods: {
        loadConfig() {
            this.$ajax
                .post(Remote.configStaffSearchAPI)
                .then(resp => {
                    if (resp && resp.data && resp.data.DisplayRows instanceof Array) {
                        resp.data.DisplayRows.sort((a, b) => {
                            return a.Order - b.Order;
                        });
                        this.infoConfig = resp.data.DisplayRows;
                    }
                })
                .catch()
                .then(() => {
                    this.loading = false;
                });
        },
        onSearchInput(evt) {
            if (this.query && this.query.length >= 2) {
                this.searching = true;

                if (this.searchRequest) {
                    this.searchRequest.cancel();
                }

                this.searchRequest = this.$ajax.CancelToken.source();

                this.$ajax
                    .post(Remote.searchAPI, `query=${this.query}`, {
                        cancelToken: this.searchRequest.token
                    })
                    .then(resp => {
                        this.employees = resp ? resp.data || [] : [];
                        if (this.emp) this.emp.expanded = false;
                        this.emp = null;
                    })
                    .catch()
                    .then(() => {
                        this.searched = true;
                        this.searching = false;
                        this.searchRequest = null;
                    });
            } else {
                this.employees = [];
                this.searched = false;
            }
        },
        onListSelectionChange(emp) {
            this.emp = emp;
            if (!emp) return;
        }
    },
    created() {
        this.loadConfig();
        this.onSearchInput = debounce(this.onSearchInput, 500);
    }
};
</script>

<style lang="scss">
//
//.search .col-search-result .emp-list .emp.expanded .emp-info {
//    display: block;
//}

.search {
    position: relative;
    display: inline-block;
    min-width: 400px;
    width: auto !important;

    .section {
        min-width: 200px;

        .form-row {
            margin: 0 !important;

            & + .form-row {
                margin-top: 10px !important;
            }
        }
    }

    .search-editor {
        width: 100% !important;
        box-sizing: border-box;
    }

    .search-result-summary {
        color: #333;
        display: inline-block;
        box-sizing: border-box;
        padding: 10px;
        width: 100%;
        background: #e0e0e0;
    }

    .row-search-result {
        font-size: 0;
    }

    .col-search-result,
    .col-emp-info {
        font-size: 12px;
        vertical-align: top;
        box-sizing: border-box;
    }

    .col-search-result {
        width: 370px;
        display: inline-block;

        .emp-info {
            display: none;
        }

        & > .emp-list > .emp {
            padding-left: 0;
        }
    }

    .col-emp-info {
        width: 370px;
        display: none;
        margin-left: 10px;
    }

    //@media (min-width: 1024px) {
        &.expanded {
            width: 780px;
        }
        .col-search-result,
        .col-emp-info {
            max-height: 600px;
            overflow-y: auto;
        }
        .col-emp-info {
            display: none;
        }
        .col-search-result {
            & > .emp-list > .emp {
                & > .emp-summary {
                    .h-icon-expand {
                        display: block;
                    }

                    .v-icon-expand {
                        display: none;
                    }
                }

                &.expanded.emp-info {
                    display: block;
                }

                &.expanded {
                    & > .emp-summary {
                        background: #9fe2ff;
                    }

                    .emp-info.expanded{
                        display: block;
                    }
                }
            }
        }
    //}
}
</style>
