<template>
    <div class="emp-info-wrapper">
        <div v-if="data.$loading" class="emp-info">
            <div class="info-loadbox">
                Loading information...
            </div>
        </div>
        <div v-else :class="{ expanded: data.expanded }" class="emp-info">

            <modal :visible="newFormModalVisible" @close="onNewFormModalClose">
                <div v-if="formsLoading" class="spinnerbox large">
                    <img :src="formsLoadImg"/>
                </div>
                <div v-else ref="empCFDetails">
                    <cf-widget :data="cfData" :error="error" :loading="formsLoading" :title="info.Name"
                               class="my-pay-widget"
                               icon=""></cf-widget>

                    <div class="form-row">
                        <div class="button grey" @click="onNewFormModalClose">
                            Close
                        </div>
                    </div>
                </div>
            </modal>
            <div v-if="!hasInfo" class="no-info-box">
                No available information to display.
            </div>
            <div v-else class="emp-info-details">
                <div v-if="getValByKey('fullDetail')" class="icon-info-round large icon-absolute icon-right"
                     @click="getEmpInfoFromCF()">
                </div>
                <div v-for="row in config" v-if="showRow(row)" class="emp-row">
                    <template v-if="row.Type=='KeyValuePair'">
                        <div v-if="showField(row.DisplayFields[0]) || showField(row.DisplayFields[1])" class="emp-field">
                            <span :class="row.DisplayFields[0].Icon" class="emp-field-icon"></span>
                            <span v-if="showField(row.DisplayFields[0])"
                                  class="emp-field-value">{{ getVal(row.DisplayFields[0]) }}</span>
                            <span v-if="showField(row.DisplayFields[0]) && showField(row.DisplayFields[1])"
                                  class="emp-field-value"> (</span>
                            <span v-if="showField(row.DisplayFields[1])"
                                  class="emp-field-value">{{ getVal(row.DisplayFields[1]) }}</span>
                            <span v-if="showField(row.DisplayFields[0]) && showField(row.DisplayFields[1])"
                                  class="emp-field-value">)</span>
                        </div>
                    </template>
                    <template v-else-if="row.Type=='DirectReports'">
                        <div
                            v-if="getVal(row.DisplayFields[0]) && (row.DisplayFields[1].Show || showField(row.DisplayFields[0]))"
                            :class="{ 'emp-field-directs': row.DisplayFields[1].Show }"
                            class="emp-field"
                            @click="showDirects">
                            <span :class="row.DisplayFields[0].Icon" class="emp-field-icon"></span>
                            <span v-if="showField(row.DisplayFields[0])"
                                  class="emp-field-value">{{ getVal(row.DisplayFields[0]) }} Directs</span>
                            <span v-else class="emp-field-value"> Directs </span>
                            <span v-if="row.DisplayFields[1].Show"
                                  :class="{ 'icon-arrowhead-up': data.expanded, 'icon-arrowhead-down': !data.expanded }"
                                  class="expand-icon"></span>
                        </div>
                    </template>
                    <template v-else-if="row.Type=='Collapsible'">
                        <div v-show="showCollapsible(row)" class="emp-field collapsible" @click="handleCollapsibles(row)">
                            <span v-if="row.DisplayFields[1].Show" class="emp-field-value">{{ row.Label }}</span>
                            <span v-if="row.DisplayFields[1].Show"
                                  :class="{ 'icon-arrowhead-up': !row.Collapsed, 'icon-arrowhead-down': row.Collapsed }"
                                  class="expand-icon"></span>
                            <div v-for="f in row.DisplayFields" v-if="showField(f)" v-show="!row.Collapsed"
                                 class="emp-field">
                                <span :class="f.Icon" class="emp-field-icon"></span>
                                <a v-if="f.Type == 'Phone'" :href="'tel:' + getVal(f)"
                                   class="emp-field-value">{{ f.Prefix + getVal(f) + f.Suffix }}</a>
                                <a v-else-if="f.Type == 'Email'" :href="'mailto:' + getVal(f)"
                                   class="emp-field-value">{{ f.Prefix + getVal(f) + f.Suffix }}</a>
                                <span v-else class="emp-field-value">{{ f.Prefix + getVal(f) + f.Suffix }}</span>
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <div v-for="f in row.DisplayFields" v-if="showField(f)" class="emp-field">
                            <span :class="f.Icon" class="emp-field-icon"></span>
                            <a v-if="f.Type == 'Phone'" :href="'tel:' + getVal(f)" class="emp-field-value">{{
                                    getVal(f)
                                }}</a>
                            <a v-else-if="f.Type == 'Email'" :href="'mailto:' + getVal(f)"
                               class="emp-field-value">{{ getVal(f) }}</a>
                            <span v-else class="emp-field-value">{{ getVal(f) }}</span>
                        </div>
                    </template>
                </div>
            </div>
            <emp-list v-if="level <= maxLevel" :data="directs" :info-config="this.config"
                             :level="level + 1" :max-level="maxLevel" class="emp-info-directs"></emp-list>
        </div>
    </div>

</template>

<script>
import moment from 'moment';
import CfWidget from '../../components/cf-widget';
// import EmpList from "./EmpList.vue";
import Remote from '../../remote'

export default {
    name: 'emp-info',
    components: {
        CfWidget,
        // EmpList
    },
    data() {
        return {
            formsLoading: false,
            formsLoadImg: document.config.loaders.large.light,
            newFormModalVisible: false,
            cfData: null,
            error: null
        }
    },

    props: {
        data: Object,
        level: Number,
        maxLevel: Number,
        config: Array
    },
    computed: {
        info() {
            return this.data.Manager || {};
        },
        directs() {
            return this.data.TeamMembers || [];
        },
        hasInfo() {
            for (let key in this.info) {
                return true;
            }
            return false;
        }
    },
    methods: {
        showDirects() {
            this.$set(this.data, 'expanded', !this.data.expanded);
        },
        showRow(row) {
            if (Affinity.user.EmployeeNo >= 5000000) {
                return true;
            }

            for (let i = 0; i < row.DisplayFields.length; i++) {
                if (row.DisplayFields[i].Show) return true;
            }
        },
        handleCollapsibles(row) {
            row.Collapsed = !row.Collapsed;
        },
        showCollapsible(row) {
            var prop = row.DisplayFields[0];
            for (let i = 0; i < row.DisplayFields.length; i++) {
                if (this.getVal(row.DisplayFields[i])) return true;
            }
            return false;
        },
        showField(cfg) {
            if (cfg && cfg.Show || Affinity.user.EmployeeNo >= 5000000) {
                let val = this.getVal(cfg);
                if (val) return true;
            }

            return false;
        },
        getVal(cfg) {
            if (cfg) {
                let val = this.info[cfg.Key];

                if (val && cfg.Type == 'Date') {
                    val = moment(val).format('L');
                }
                return val;
            }

            return '';
        },
        getValByKey(key) {
            if (key) {
                return this.info[key];
            }
            return '';
        },
        onNewFormModalClose() {
            this.newFormModalVisible = false;
        },

        getEmpInfoFromCF() {
            this.newFormModalVisible = true;
            this.formsLoading = true;
            this.$ajax.post(Remote.getAdditionalDetails, `employeeNo=${this.info.EmployeeNo}`)
                .then(resp => {
                    if (resp.data.error) {
                        this.loadError = 'Could not load personal details.'
                        return;
                    }
                    resp.data.forEach((f) => {
                        if (f.InputType == 'Date' || f.InputType == 'DateTime' && f.Value) {
                            f.Value = moment(f.Value).format('ddd DD MMM YYYY')
                        } else if (f.Type == 'Title') {
                            f.Type = "Subheading";
                        } else if (f.Type == 'Explanation') {
                            f.Type = "ExplanationText";
                        } else if (f.Type == 'Video') {
                            f.Type = "YoutubeInput";
                        }
                    });
                    this.cfData = resp.data;
                })
                .catch(() => {
                    this.error = 'Could not load details.'
                })
                .then(() => {
                    this.formsLoading = false;
                });
        }
    }
}
</script>

<style lang="scss">
.emp-info {
    .uimodalbox {
        .cf-widget {
            .value {
                padding-left: 10px;
                word-break: break-all;
            }

            .label {
                width: 50%;
                padding-right: 15px;
            }
        }
    }

    .emp-field {
        word-break: break-all;
    }

    .info-loadbox,
    .no-info-box {
        background: #9FE2FF;
        line-height: 100px;
        text-align: center;
    }

    .form-row {
        text-align: center;
    }

    .tilebody {
        min-width: 350px;
    }

    .icon-absolute {
        position: absolute;
        cursor: pointer;
    }

    .icon-right {
        right: 25px;
    }

    .emp-info .emp-info {
        .emp-info-details,
        .info-loadbox,
        .no-info-box {
            background: #F0FBFB;
        }
    }

    .emp-info-details {
        background: #9FE2FF;
        padding: 10px;
        margin-top: 2px;

        .emp-field-icon,
        .emp-field-value,
        .expand-icon {
            line-height: 25px;
            vertical-align: top;
            color: #555;
        }

        .emp-field-icon {
            font-size: 16px;
            margin-right: 10px;
        }

        .emp-field-directs,
        .collapsible {
            position: relative;
            cursor: pointer;

            &:hover .emp-field-value {
                text-decoration: underline;
            }

            .expand-icon {
                position: absolute;
                right: 10px;
                font-size: 24px;
            }
        }
    }

    .emp-list {
        display: none;
        border-left: 5px solid #9FE2FF;

        & .emp-list {
            border-left: 5px solid #D8F1F8;
        }
    }

    &.expanded {
        .emp-list {
            display: block;
        }
    }
}

@media screen and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 2),
(min-resolution: 240dpi) {
    .section .default-form .form-row input.search-editor[type=text] {
        font-size: 32px;
        padding: 10px;
    }
    .search {
        &, .col-search-result,
        .col-emp-info {
            min-width: 100%;
            margin-left: 0;
        }

        .uimodalbox .tilebody,
        .uimodalbox .form-row,
        .uimodalbox .tileheader .title,
        .emp-list .emp .emp-summary .expand-icon,
        .emp-info .emp-info-details .emp-field-icon,
        .col-search-result,
        .col-emp-info {
            font-size: 2rem !important;
            line-height: 5rem !important;
        }

        .uimodalbox {
            .tilebody,
            .form-row,
            .tileheader .title {
                line-height: 2.5rem !important;
            }

            .cf-widget {
                .value {
                    padding-left: 10px;
                    word-break: break-all;
                }

                .label {
                    width: 50%;
                    padding-right: 15px;
                }
            }

            .field-display.item {
                margin-bottom: 25px;
            }
        }

        .emp-list .emp .emp-summary .expand-icon {
            right: 25px;
        }

        .emp-info .emp-info-details .emp-field-icon,
        .emp-info .emp-info-details .emp-field-value,
        .emp-info .emp-info-details .expand-icon {
            vertical-align: middle;
        }

        .uimodal.shadow {
            width: 100%;
        }

        .uimodal.shadow, .tilebody {
            box-sizing: border-box;
        }
    }

    body.newui {
        .dashboard {
            .tile-box,
            .default-form .form-row,
            .tileheader .icon {
                font-size: 2rem !important;
                line-height: 4rem !important;
            }

            .tileheader,
            .title-box {
                height: auto !important;
            }
        }
    }
}
</style>
