<template>
    <div>
        <tile
            :actions="actions"
            :loading="loading"
            class="bank-accounts"
            icon="icon-dollar-round"
            title="Bank Accounts">
            <div class="section">
                <div class="section-body">
                    <draggable :list="accounts" :options="{ handle: '.title-text' }" class="default-form">
                        <div v-for="(acc, index) in accounts" :key="acc.id" class="account">
                            <div :class="{ primary: index == 0 }" class="title-row section-title">
                                <span v-if="index == 0" class="title-text">Primary Bank Account</span>
                                <span v-else class="title-text">Additional Bank Account {{ index }}</span>
                            </div>
                            <account
                                ref="accounts"
                                :country="country"
                                :data="acc"
                                :showAmount="index > 0"
                                validationUrl="/api/Profile/ValidateBankAccount">
                            </account>
                            <x-switch
                                v-if="index > 0"
                                :value="acc.Flag == '120'"
                                class="acc-switch"
                                @input="onAccountStateChange(acc, $event)">
                            </x-switch>
                            <button
                                v-if="accounts.length >1 && (acc.Name || acc.Number || acc.Amount || acc.Flag)"
                                class="btn-rem button red"
                                @click="showModal(index)">
                                <span class="b-icon icon-trash"></span>
                                <span>Clear</span>
                            </button>
                        </div>
                    </draggable>
                    <div class="section-bottom">
                        <div v-if="submitting" class="loader-box"></div>
                        <button v-else class="button green" @click="save()">
                            <span class="b-icon icon-save"></span>
                            <span>Save</span>
                        </button>
                    </div>
                </div>
            </div>
        </tile>

        <div v-if="showDialog" class="confirm-dialog">
            <div>
                <h3>By clearing out these bank account details, no funds can be paid into this account. Do you want to continue?</h3>
            </div>
            <div class="dialog-button">
                <button class="button green" @click="confirmDialog(true)">Continue</button>
                <button class="button grey"  @click="confirmDialog(false)">Clear</button>
            </div>
        </div>
    </div>

</template>

<script>
import {mapActions} from "vuex";
import Account from "./account.vue";
import _ from "lodash";

const EMPTY_ACCOUNT = {
    Name: null, Number: null, Amount: null, Flag: null
}

export default {
    components: {
        Account
    },
    created() {
        this.getAccounts();
    },

    data() {
        return {
            accounts: [],
            originAccounts: [],
            loading: true,
            submitting: false,
            country: "",
            counter: 0,
            maxAccounts: 0,
            showDialog: false,
        };
    },
    computed: {
        actions() {
            let actions = [];

            if (this.accounts.length < this.maxAccounts) {
                actions.push({
                    icon: "icon-add-to-list",
                    label: "Add an account",
                    click: () => {
                        this.addAccount(false);
                    }
                });
            }

            return actions;
        },

    },
    methods: {
        ...mapActions(["addToast", "showRequestError"]),
        getAccounts() {
            this.$ajax
                .post("/api/Profile/GetBankAccounts")
                .then(resp => {
                    this.country = resp.data.Country;
                    this.accounts = resp.data.Accounts;
                    this.maxAccounts = resp.data.MaxAccounts;
                    this.addEmptyAccount(true);
                    (this.accounts || []).forEach(a => {
                        a.id = ++this.counter;
                    });
                    this.originAccounts = _.cloneDeep(this.accounts)
                })
                .catch()
                .then(() => {
                    this.loading = false;
                });
        },
        addEmptyAccount() {
            for (let i = this.accounts.length; i < this.maxAccounts; i++) {
                this.addAccount(true);
            }
        },
        addAccount(isEmpty) {
            if (isEmpty) {
                this.accounts.push(_.clone(EMPTY_ACCOUNT))
                return;
            }

            this.accounts.push({
                Flag: "120"
            });
        },

        compareRemovedChange(index) {
            const toRemove = this.accounts[index];
            const origin = this.originAccounts[index];

            if (!origin) return false;

            return toRemove.Amount === origin.Amount
                && toRemove.Flag === origin.Flag
                && toRemove.Name === origin.Name
                && toRemove.Number === origin.Number;
        },

        preventRemoveEmpty(index) {
            const origin = this.originAccounts[index];
            if ( origin.Amount === null
                && origin.Flag === null
                && origin.Name === null
                && origin.Number === null) {
                return true;
            }
        },

        showModal(clearModalIndex) {
            this.clearModalIndex = clearModalIndex;
            this.showDialog = true;
        },

        confirmDialog(success) {
            this.showDialog = false;
            if (success) {
                this.clearAccount(this.clearModalIndex)
            }
        },

        spliceAccount(index) {
            const cloneAccount = _.clone(EMPTY_ACCOUNT)
            cloneAccount.id = this.counter + 1;
            this.accounts.splice(index, 1, cloneAccount)
        },

        clearAccount(index) {
            const toClearAccount = this.accounts[index];
            // do not send save request if only clear new creating account.
            if (this.preventRemoveEmpty(index)) {
                this.spliceAccount(index)
                return;
            }

            this.spliceAccount(index)
            this.submitting = true;
            this.$ajax
                .post("/api/Profile/SaveBankAccounts", JSON.stringify(this.accounts))
                .then(resp => {
                    this.accounts[0].Flag = "120";

                    this.addToast({
                        type: "success",
                        title: "Account is Clear",
                        message: "Clear Account has been successfully saved."
                    });
                    this.originAccounts = _.cloneDeep(this.accounts)
                })
                .catch(e => {
                    this.showRequestError({
                        resp: e.response,
                        title: "Could not update accounts."
                    });
                    this.accounts.splice(index, 1, toClearAccount)
                })
                .then(() => {
                    this.submitting = false;
                });
        },
        save() {
            let valid = true;

            (this.$refs.accounts || []).forEach(acc => {
                if (!acc.accountNumber && !acc.bankCode && !acc.branchCode) {
                    // continue;
                } else {
                    valid &= acc.validate();
                }

            });

            // fix flag null when edit multiple
            this.accounts.forEach(account => {
                if (account.Name
                    && account.Number
                    && account.Amount
                    && account.Flag === null) {
                    account.Flag = "130" // set toggle button to false.
                }
            })

            if (valid) {
                this.submitting = true;
                this.$ajax
                    .post("/api/Profile/SaveBankAccounts", JSON.stringify(this.accounts))
                    .then(resp => {
                        this.accounts[0].Flag = "120";

                        this.addToast({
                            type: "success",
                            title: "Accounts are updated",
                            message: "Accounts have been successfully updated."
                        });
                    })
                    .catch(e => {
                        this.showRequestError({
                            resp: e.response,
                            title: "Could not update accounts."
                        });
                    })
                    .then(() => {
                        this.submitting = false;
                    });
            } else
                this.addToast({
                    type: "error",
                    title: "Cannot save accounts",
                    message:
                        "Some accounts have invalid information entered. Please correct all errors and try again."
                });
        },
        onAccountStateChange(account, state) {
            account.Flag = state ? "120" : "130";
        },
        resetAccounts() {
            this.accounts = [];
            this.counter = 0;
        }
    },
};
</script>

<style lang="scss">
.bank-accounts {
    .title-row {
        margin: 0 0 15px 0 !important;

        .title-text {
            cursor: move;
        }
    }

    .account {
        position: relative;
        border: 1px dashed #aaa;
        padding: 10px;

        & + .account {
            margin-top: 10px;
        }

        .btn-rem {
            position: absolute;
            right: 15px;
            top: 15px;
            cursor: pointer;
        }

        .acc-switch {
            position: absolute;
            right: 105px;
            top: 14px;
        }
    }

    .section-bottom {
        margin-top: 30px;
    }
}

.confirm-dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 500px;
    margin-left: -250px;
    margin-top: -140px;
    background: white;
    border-radius: 10px;
    padding: 20px;
}

.dialog-button {
    display: flex;
    justify-content: space-around;
    max-width: 200px;
    margin: 0 auto;
}
</style>
