<script>
    import Vue from 'vue';
    import moment from 'moment';
    import 'moment/locale/en-au';
    import 'moment/locale/en-nz';

    export default {
        model: {
            prop: 'date',
            event: 'input'
        },
        props: {
            date: {
                type: Date,
                default: null
            },
            format: {
                type: String,
                default: 'll'
            },
            editFormat: {
                type: String,
                default: 'L'
            },
            allowNull: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                day: 0,
                year: 0,
                month: 0,
                scope: 2,
                months: [],
                editing: false,
                value: '',
                calX: 0,
                calY: 0,
                calShow: false,
                preventClose: false,
                original: null
            }
        },
        computed: {
            today() {
                return moment().startOf('day');
            },
            daysOfWeek() {
                let days = [];
                let date = moment();

                for (let i = 0; i < 7; i++) {
                    let d = date.day(i).format('ddd');
                    days.push(d)
                }

                return days;
            },
            daysInMonth() {
                let days = [];
                let monthDate = moment([this.year, this.month])
                let daysCount = monthDate.daysInMonth();
                let offset = monthDate.day();

                for (let i = 0; i < offset; i++) {
                    days.push(null);
                }

                for (let i = 1; i <= daysCount; i++) {
                    let date = moment([this.year, this.month, i]);
                    days.push(date);
                }

                return days;
            },
            years() {
                let years = [];
                let decade = Math.floor(this.year / 10);

                for (let i = 0; i < 10; i++) {
                    years.push(decade * 10 + i);
                }

                return years;
            },
            scopeTitle() {
                if (this.scope === 0) {
                    let decade = Math.floor(this.year / 10);
                    return `${decade}0s`;
                }

                let date = [];
                let format = '';

                if (this.scope > 0) {
                    date.push(this.year);
                    format = 'YYYY';
                }
                if (this.scope > 1) {
                    date.push(this.month);
                    format = 'MMMM ' + format;
                }

                return moment(date).format(format);
            }
        },
        watch: {
            date(date) {
                if (this.editing) return;

                this.decomposeDate(date);
                this.setValue();
            }
        },
        methods: {
            openCalendar() {
                if (!this.editing || this.calShow) return;

                this.calShow = true;
                this.decomposeDate(this.date);

                let brect = this.$refs.editor.getBoundingClientRect();
                this.calX = brect.left;
                this.calY = brect.top + brect.height;

                Vue.nextTick(() => {
                    let crect = this.$refs.calendar.getBoundingClientRect();

                    if (brect.top + crect.height > window.innerHeight) this.calY = brect.top - crect.height;
                })
            },
            closeCalendar() {
                this.calShow = false;
            },
            composeDate() {
                return moment([this.year, this.month, this.day]);
            },
            decomposeDate(date) {
                date = this.getDate(date);
                if (!date) date = moment();

                this.day = date.date();
                this.year = date.year();
                this.month = date.month();
            },
            incrementDate(reverse) {
                let inc = reverse ? -1 : 1;
                let date = moment([this.year, this.month, this.day]);

                switch (this.scope) {
                    case 0:
                        date = date.add(inc * 10, 'y');
                        break;
                    case 1:
                        date = date.add(inc, 'y');
                        break;
                    case 2:
                        date = date.add(inc, 'M');
                        break;
                }

                this.decomposeDate(date);
            },
            setValue() {
                let date = this.date || !this.allowNull ? this.getDate() : null;
                this.value = date ? date.format(this.editing ? this.editFormat : this.format) : '';
            },
            getDate(date) {
                if (date === null) {
                    if (!this.allowNull) date = new Date();
                    else return null;
                }
                else if (date === undefined) date = this.date;

                return moment(date).startOf('day');
            },
            parseDate() {
                let value = this.$refs.editor.value;
                if (!value && this.allowNull) return null;

                let date = moment(value, this.editFormat);
                return date.isValid() ? date : false;
            },
            emitChange(date) {
                date = this.getDate(date);

                let d = date;
                let changed = false;

                if (d === null) {
                    changed = this.original !== null;
                }
                else if (this.original === null) {
                    changed = true;
                }
                else if (!this.original.isSame(date)) {
                    d = date.toDate();
                    changed = true;
                }

                if (changed) {
                    this.$emit('input', d);
                    this.$emit('change', d);
                }

                this.original = date;
            },
            stopEdit(evt) {
                this.editing = false;
                this.closeCalendar();
                this.setValue();
            },
            onEditFocus() {
                if (!this.editing) {
                    this.original = this.getDate();
                    this.editing = true;
                    this.setValue();
                    this.openCalendar();
                    Vue.nextTick(() => {
                        this.$refs.editor.select();
                    });
                }
            },
            onEditInput() {
                this.openCalendar();

                let date = this.parseDate();

                if (date) date = date.toDate();
                else if (date === false || !this.allowNull) date = new Date();

                this.decomposeDate(date);

                this.$emit('input', date);
            },
            onEditKeydown(evt) {
                switch (evt.key) {
                    case 'Enter':
                        let date = this.parseDate();
                        if (date !== false) this.emitChange(date);
                        else this.setValue();
                    case 'Escape':
                        this.closeCalendar();
                        break;
                    case 'Tab':
                        this.stopEdit();
                        break;
                }
            },
            onCalendarSelect(preventFocus) {
                let date = moment([this.year, this.month, this.day]);
                if (!preventFocus) this.$refs.editor.focus();

                this.emitChange(date);

                Vue.nextTick(() => {
                    this.setValue();
                });
            },
            onScopeClick() {
                if (this.scope > 0) this.scope--;
            },
            onPrevClick() {
                this.incrementDate(true);
            },
            onNextClick() {
                this.incrementDate();
            },
            onDayClick(day) {
                if (!day) return;
                this.day = day.date();
                this.onCalendarSelect(true);
                this.stopEdit();
            },
            onMonthClick(month) {
                this.month = month;
                this.scope++;
                this.onCalendarSelect();
            },
            onYearClick(year) {
                this.year = year;
                this.scope++;
                this.onCalendarSelect();
            },
            onMousedown() {
                this.preventClose = true;
            },
            onBodyMousedown() {
                if (!this.editing || this.preventClose) {
                    this.preventClose = false;
                    return;
                }

                this.stopEdit();

                let date = this.parseDate(this.value);
                if (date !== false) this.emitChange(date);
            }
        },
        created() {
            this.setValue();

            for (let i = 0; i < 12; i++) {
                let month = moment().month(i).format('MMMM');
                this.months.push(month);
            }

            document.addEventListener('mousedown', this.onBodyMousedown);
            document.addEventListener('scroll', this.closeCalendar);
        },
        beforeDestroy() {
            document.removeEventListener('mousedown', this.onBodyMousedown);
            document.removeEventListener('scroll', this.closeCalendar);
        }
    }
</script>

<template>
    <div class="datepicker" @mousedown="onMousedown">
        <div class="input-box">
            <input ref="editor" type="text" v-model="value" @mousedown="openCalendar()"
                @focus="onEditFocus" @input="onEditInput" @keydown="onEditKeydown" />
        </div>
        <div ref="calendar" class="calendar" v-if="editing && calShow" :style="{ left: calX + 'px', top: calY + 'px' }">
            <div class="calendar-head">
                <div class="calendar-nav">
                    <div class="cell icon-arrowhead-left" @click="onPrevClick"></div>
                    <div class="scope-display" @click="onScopeClick">{{ scopeTitle }}</div>
                    <div class="cell icon-arrowhead-right" @click="onNextClick"></div>
                </div>
            </div>
            <div class="calendar-body">
                <div class="grid days" v-if="scope == 2">
                    <div class="days-of-week">
                        <div v-for="dow in daysOfWeek" class="cell cell-dow">{{ dow }}</div>
                    </div>
                    <div v-for="d in daysInMonth" class="cell cell-day"
                        :class="{ empty: !d, today: d && d.isSame(today), selected: d && d.date() == day }"
                        @click="onDayClick(d)">{{ d ? d.date() : '' }}</div>
                </div>
                <div class="grid months" v-else-if="scope == 1">
                    <div v-for="(m, i) in months" class="cell cell-month" :class="{ selected: i == month }" @click="onMonthClick(i)">{{ m }}</div>
                </div>
                <div v-else class="years">
                    <div v-for="y in years" class="cell cell-year" :class="{ selected: y == year }" @click="onYearClick(y)">{{ y }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
    .datepicker {
        * {
            box-sizing: border-box;
        }
        .calendar {
            width: 280px;
            position: fixed;
            font-size: 0;
            background-color: white;
            -webkit-box-shadow: 0 3px 2px rgba(0,0,0,.4);
            -moz-box-shadow: 0 3px 2px rgba(0,0,0,.4);
            box-shadow: 0 3px 2px rgba(0,0,0,.4);
            z-index: 9999;

            .cell {
                width: 40px;
                height: 30px;
                line-height: 30px;
                font-size: 12px;
                display: inline-block;
                text-align: center;
                vertical-align: top;
                cursor: pointer;
            }
            .calendar-nav {
                display: table;
                width: 100%;
                cursor: pointer;

                .cell {
                    display: table-cell;
                    font-size: 22px;
                }
                .scope-display {
                    font-size: 18px;
                    text-align: center;
                    line-height: 30px;
                }
            }
            .calendar-body {
                .cell {
                    &.selected {
                        color: #fff;
                        background-color: #ff6600 !important;
                    }
                    &:hover {
                        color: #fff;
                        background-color: #ffbd00;
                    }
                }
                .cell-dow {
                    cursor: default;
                    color: black !important;
                    background-color: transparent !important;
                }
                .cell-day {
                    &.today {
                        color: #fff;
                        background-color: #44b5ec;
                    }
                    &.empty {
                        cursor: default;
                        background-color: transparent;
                    }
                }
                .cell-month {
                    width: 33.33%;
                }
                .cell-year {
                    width: 70px;
                }
            }
        }
    }
</style>
